import { Component } from '@angular/core';
import { environment } from 'projects/browser-extension/src/environments/environment';
import { EAuthenticateActionButtons } from '../../enums/authenticate-actions.enum';
import { ERegisterationSource } from '@ngx-common/enums/registeration-source.enum';

@Component({
	selector: 'app-login-page',
	templateUrl: './login-page.component.html',
	styleUrls: ['./login-page.component.scss'],
})
export class LoginPageComponent {
	eAuthenticateActionButtons = EAuthenticateActionButtons;

	goToLink(btnClicked: EAuthenticateActionButtons) {
		let tabURl: string;
		const successRedirect = decodeURIComponent('/browser-extension/login/successful');
		switch (btnClicked) {
			case EAuthenticateActionButtons.ConnectWithGoogle:
				tabURl = `${environment.dashboardWebsite}/login-redirect?isLogin=true&ssoProvider=1&redirect=${successRedirect}&regSource=${ERegisterationSource.AiDetectorExtension}`;
				break;
			case EAuthenticateActionButtons.ConnectWithApple:
				tabURl = `${environment.dashboardWebsite}/login-redirect?isLogin=true&ssoProvider=0&redirect=${successRedirect}&regSource=${ERegisterationSource.AiDetectorExtension}`;
				break;
			case EAuthenticateActionButtons.ManualSignup:
				tabURl = `${environment.dashboardWebsite}/signup?redirectUrl=${successRedirect}&regSource=${ERegisterationSource.AiDetectorExtension}`;
				break;
			case EAuthenticateActionButtons.ManualLogin:
				tabURl = `${environment.dashboardWebsite}/login-redirect?isLogin=true&redirect=${successRedirect}`;
				break;
			default:
				return;
		}
		chrome.tabs.create({ url: tabURl });
	}
}
